.App {
  text-align: center;
  color: white;
  background: url("https://res.cloudinary.com/dvd0xwpmc/image/upload/v1539531286/showbusiness/cityligths.jpg");
  background-size: cover; }

#mainMap {
  border-radius: 2%;
  text-decoration: none;
  text-shadow: 0px 0px black; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-main {
  min-height: 100vh;
  width: 90%;
  margin: 50px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  font-weight: bolder;
  color: white;
  padding-top: 40px;
  padding-bottom: 20px;
  text-shadow: 2px 2px black; }
  .App-main h3 {
    font-weight: bolder;
    text-shadow: 2px 2px black; }
  .App-main h1 {
    font-weight: bolder;
    text-shadow: 2px 2px black; }

.App-link {
  color: #61dafb; }

a {
  color: white;
  cursor: pointer; }

fieldset {
  border: 0; }

.navbar-brand img {
  width: 50px;
  height: 50px; }

.nav-link img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 5px; }

.nav-link #logoutlink {
  margin: 0 auto; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

/* Footer */
section {
    padding: 30px 0;
}

section .section-title {
    text-align: center;
    color: #007b5e;
    margin-bottom: 10px;
    text-transform: uppercase;
}
#footer {
    background: #007b5e !important;
}
#footer h5{
	padding-left: 10px;
    border-left: 3px solid #eeeeee;
    padding-bottom: 6px;
    margin-bottom: 20px;
    color:#ffffff;
}
#footer a {
    color: #ffffff;
    text-decoration: none !important;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}
#footer ul.social li{
	padding: 3px 0;
}
#footer ul.social li a i {
    margin-right: 5px;
	font-size:25px;
	transition: .5s all ease;
}
#footer ul.social li:hover a i {
	font-size:30px;
	margin-top:-10px;
}
#footer ul.social li a,
#footer ul.quick-links li a{
	color:#ffffff;
}
#footer ul.social li a:hover{
	color:#eeeeee;
}
#footer ul.quick-links li{
	padding: 3px 0;
	transition: .5s all ease;
}
#footer ul.quick-links li:hover{
	padding: 3px 0;
	margin-left:5px;
	font-weight:700;
}
#footer ul.quick-links li a i{
	margin-right: 5px;
}
#footer ul.quick-links li:hover a i {
    font-weight: 700;
}

@media (max-width:767px){
	#footer h5 {
    padding-left: 0;
    border-left: transparent;
    padding-bottom: 0px;
    margin-bottom: 10px;
}
